<template>
  <div v-theme:column="'narrow'" id="showBlogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <div v-for="blog in filteredBlogs" class="singleBlog">
    	<router-link v-bind:to="'/blog/' + blog.id">
    		<h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
    	</router-link>
    	<article>
    		{{blog.content | snippet}}
    	</article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'showBlogs',
  data(){
  	return {
  		blogs:[],
  		search:""
  	}
  },
  created(){
  	this.$http.get('https://vuedemo-fcd8b.firebaseio.com/posts.json').then(function(data){
  			// console.log(data.json());
  			return data.json()
  			// this.blogs = data.body.slice(0,10);
  			// console.log(this.blogs);
  	}).then(function(data){
  		var blogsArray = [];
  		for(let key in data){
  			// console.log(key)
  			// console.log(data[key])
  			data[key].id = key;
  			blogsArray.push(data[key]);
  		}
  		// console.log(blogsArray)
  		this.blogs = blogsArray;
  	})
  	// this.$http.get('./../static/posts.json').then(function(data){
  	// 		this.blogs = data.body.slice(0,10);
  	// 		console.log(this.blogs);
  	// })
  	// this.$http.get('https://jsonplaceholder.typicode.com/posts').then(function(data){
  	// 		this.blogs = data.body.slice(0,10);
  	// 		console.log(this.blogs);
  	// })
  },
  computed:{
  	filteredBlogs:function(){
  		return this.blogs.filter((blog) => {
  			return blog.title.match(this.search);
  		})
  	}
  },
  filters:{
  	// "to-uppercase":function(value){
  	// 	return value.toUpperCase();
  	// }

  	//ES6中的方法
  	toUppercase(value){
  		return value.toUpperCase();
  	}
  }
}
</script>

<style>
#showBlogs{
	max-width: 800px;
	margin: 0 auto;
}

.singleBlog{
	padding: 20px;
	margin: 20px 0;
	box-sizing: border-box;
	background: #eee;
	border: 1px dotted #aaa;
}

#showBlogs a{
	color: #444;
	text-decoration: none;
}

input[type="text"]{
	padding: 8px;
	width: 100%;
	box-sizing: border-box;
}
</style>



